<template>
  <v-card outlined class="mt-5" dark max-width="800px" width="100%">
    <v-img :src="post.imageSrc" width="100%" />
    <v-card-title class="text-h3">
      {{ post.title }}
    </v-card-title>
    <v-divider></v-divider>
    <v-card-subtitle
        class="d-flex justify-space-between"
    >
      <div class="attrFont">
        <v-icon style="margin-bottom: 2px; margin-right: 4px;" small>far fa-eye</v-icon>
        <span>{{ post.views }}</span>
      </div>
      <div class="attrFont">{{ post.creationDate }}</div>
    </v-card-subtitle>
    <v-card-subtitle
        class="d-flex justify-end attrFont pt-0 pb-0"
        v-if="post.updated"
    >updated
    </v-card-subtitle>
    <v-card-subtitle class="text-h5" v-html="post.postBody"></v-card-subtitle>
  </v-card>
</template>

<script>
export default {
  name: "PostMain",
  data() {
    return {
      post: []
    }
  },
  methods: {

  },
  async created() {
    try {
      let res = await fetch(`http://localhost:8000/list/${this.$route.params.id}`, {
        method: 'GET'
      })
      this.post = await res. json()
      await fetch(`http://localhost:8000/list/${this.$route.params.id}`, {
        method: 'PUT',
        headers: {
          'Content-type': 'application/json'
        },
        body: JSON.stringify({
          title: this.post.title,
          postBody: this.post.postBody,
          updated: false,
          views: ++this.post.views,
          imageSrc: this.post.imageSrc
        })
      })
    } catch (e) {
      throw new Error(e)
    }
  }
}
</script>

<style scoped>
.attrFont {
  opacity: .8;
  font-size: 15px;
}
</style>
